@mixin desktop(){
	@media (min-width: 1367px){
		@content
	}
}

@mixin ipadPro2{
	@media (max-width: 1366px){
		@content
	}
}

@mixin ipadPro{
	@media (max-width: 1024px){
		@content
	}
}

@mixin ipad{
	@media (max-width: 768px){
		@content
	}
}

@mixin iphone8{
	@media (max-width: 414px){
		@content
	}
}

@mixin iphone5{
	@media (max-width: 320px){
		@content
	}
}

/*nav*/
.navbar li:nth-child(1) a{
	color: #563529;
	@include iphone8(){
		color: #eee;
	}
}

/*banner start*/
.bg{
	width: 100%;
	background-image: url('../image/bg/story.png');
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: 0 -100px;
	margin-top: 95px;
	@include desktop(){
		margin-top: 110px;
	}
	@media (max-width: 823px){
		margin-top: 65px;
	}
	@include iphone8(){
		margin-top: 80px;
	}
}
.bg figure{
	width: 50%;
	display: block;
	margin: auto;
	@include desktop(){
		width: 60%;
	}
	@include iphone8(){
		width: 100%;
	}
}
.bg img{
	width: 100%;
	vertical-align: middle;
	margin: auto;
}
/*banner end*/

/*====================================*/

/*main start*/
.news{
	width: 100%;
	padding: 10px;
	background-color: rgb(69,43,33);
	div{
		margin-left: 75px;
	}
	h3{
		padding: 0 10px;
		color: #DECDA4;
		font-size: 16px;
		font-weight: normal;
		font-family: Microsoft JhengHei;
		display: inline-block;
		border-right: 1px solid #ddd;
	}
	marquee{
		vertical-align: middle;
    width: 300px;
		margin-left: 2px;
		padding: 0 5px;
		font-size: 16px;
		font-family: Microsoft JhengHei;
		color: #fff;
	}
	@include desktop(){
		h3{
			font-size: 24px;
		}
		marquee{
			font-size: 24px;
		}
	}
	@include ipadPro(){
		div{
			margin-left: 12px;
		}
	}
	@media (max-width: 823px){
		h3{
			font-size: 14px;
		}
		marquee{
			font-size: 14px;
		}
	}
	@include ipad(){
		div{
			margin-left: 15px;
		}
		h3{
			font-size: 14px;
		}
		marquee{
			font-size: 14px;
		}
	}
	@include iphone8(){
		div{
			margin: 0;
		}
		marquee{
			width: 200px;
		}
	}
	@include iphone5(){
		h3{
			font-size: 12px;
		}
		marquee{
			font-size: 12px;
		}
	}
}
.mainArea{
	width: 100%;
	background: url(../image/bg/bg_main.png);
	padding: 20px;
	@media (max-width: 1200px){
		padding: 20px 0px;
	}
}
.clearfix{
	clear: both;
}
.mainBox{
	width: 90%;
	margin: auto;
}

/*titleBox start*/
.titleBox{
	width: 19%;
	padding: 10px;
	margin-right: 10px;
	float: left;
	ul{
		border-top: 1px dashed #777;
		border-bottom: 1px dashed #777;
		li{
			cursor: pointer;
			margin: 10px 0; 
		}
	}
	@media (max-width: 823px){
		width: 16%;
		padding: 0;
	}
	@include ipad(){
		float: unset;
		width: 100%;
		margin: auto;
		ul{
			border-top: none;
			border-bottom: 1px solid #aaa;
		}
	}
	@include iphone8(){
		float: unset;
		width: 100%;
		height: auto;
		margin: auto;
		ul{
			border-top: none;
			border-bottom: 1px solid #aaa;
		}
	}
}
.brand{
	padding: 20px 0;
	em{
		font-family: 'Dosis', sans-serif;
		font-style: unset;
		font-weight: normal;
	}
	strong{
		display: block;
		font-size: 2.7vmin;
		letter-spacing: 5px;
		font-family: Microsoft JhengHei;
		font-weight: normal;
	}
	@include ipad(){
		width: 240px;
		position: relative;
		strong{
			display: inline-block;
			font-size: 14px;
		}
		strong:after{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 20px;
			width: 202px;
			border-bottom: 2px solid #3B1614;
		}
	}
}
.side_menu li{
	font-size: 16px;
	letter-spacing: 1.3px;
	a{
		display: block;
    font-size: 15px;
		font-family: Microsoft JhengHei;
    color: #452B21;
    border-radius: 5px;
    padding: 10px;
    transition: all .3s linear;
	}
	
	a:hover{
		background: #452B21;
		color:#fff;
	}
	a:before{
		content: "";
		width: 3px;
    height: 3px;
    margin-right: 10px;
    display: inline-block;
    vertical-align: middle;
    background: #452B21;
    border-radius: 100%;
	}
	a:hover:before{
		background:#fff;
	}
	a.active{
		background: #452B21;
	  color: #fff;
	}
	a.active:before{
		background:#fff;
	}
	@include desktop(){
		a{
			font-size: 22px;
		}
	}
	@include ipad(){
		width: 200px;
	}
}
/*titleBox end*/

/*==========================================*/

/*storyBox start*/
.storyBox{
	width: 80%;
	float: left;
	@include ipadPro(){
		width: 79%;
	}
	@media (max-width: 823px){
		width: 82.5%;
	}
	@include ipad(){
		width: 100%;
		float: unset;
	}
}
.content{
	padding: 20px;
	@media (max-width: 823px){
		padding: 0;
		h3{
			margin-top: 15px;
		}
	}
}

/*boxA start*/
.boxA{
	padding: 0 20px;
	margin-bottom: 20px;
	h3{
		display: block;
		background: url('../image/beans.png') no-repeat left center;
		background-size: 30px;
		min-height: 40px;
		padding-left: 50px;
	}
	strong{
		line-height: 40px;
	}
	figure{
		width:100%;
		margin-top: 30px;
	}
	figure img{
		width: 100%;
		border-radius: 15px;
		vertical-align: middle;
	}
	@include desktop(){
		font-size: 26px;
	}
	@media (max-width: 736px){
		figure{
			width: 80%;
			margin: auto;
			margin-top: 30px;
		}
	}
	@include iphone8(){
		width: 100%;
		padding: 0;
		figure{
			width: 100%;
			img{
				border-radius: 5px;
			}
		}
	}

}
.storyText{
	margin-top: 80px;
	p{
		margin: 5px 0;
		text-align: center;
		font-family: 新細明體, sans-serif;
		font-size: 18px;
		color: #555;
	}
	@include desktop(){
		p{
			font-size: 26px;
		}
	}
	@media (max-width: 823px){
		p{
			font-size: 16px;
		}
	}

	@include iphone8(){
		p{
			font-size: 16px;
			text-align: center;
		}
	}
	@include iphone5(){
		p{
			font-size: 14px;
		}
	}
}

.group{
	width: 100%;
	margin-top: 80px;
	position: relative;
		figure{
			margin: 0;
			width: 70%;
			box-shadow: 2px 2px 5px rgba(0,0,0,.4);
		}
		figure img{
			width: 100%;
			vertical-align: middle;
			border-radius: 0px;
		}
	@media (max-width: 823px){
		figure{
			width: 85%;
		}
	}
	@include ipad(){
		figure{
			width: 70%;
		}
	}
	@include iphone8(){
		position: unset;
		figure{
			width: 100%;
			box-shadow: none;
		}
	}
	
}
.group:nth-of-type(odd) figure{
		position: relative;
		left: 30%;
		@media (max-width: 823px){
			left: 15%;
		}
		@include iphone8(){
			position: unset;
		}
}
.group .txt{
	width: 45%;
	padding: 10px 30px 20px;
	margin-top: 40px;
	position: absolute;
	top: 0;
	right: 30px;
  font-family: Microsoft JhengHei;
	background-color: rgba(255,255,255,.7);
	box-shadow: 2px 2px 10px 2px rgba(0,0,0,.4);
	transition: all .5s;
	z-index: 2;
	overflow: hidden;

	div{
		h4{
			display: inline-block;
			font-weight: normal;
			z-index: 2;
			font-family: tahoma, sans-serif;
		}
		span{
			font-size: 16px;
			z-index: 2;
		}
		h4:first-letter{
			font-size: 32px;
			/*color: #8a5742;*/
			z-index: 2;
		}
	}
	p{
		font-size: 16px;
		line-height: 22px;
		z-index: 2;
	}
	@include desktop(){
		div{
			span{
				font-size: 20px;
			}
		}
		p{
			font-size: 20px;
			line-height: 26px;
			letter-spacing: 1.3px;
		}
	}
	@media (max-width: 823px){
		width: 70%;
		right: 0px;
		margin-top: 30px;
		div{
			span{
				font-size: 14px;
			}
		}
		p{
			font-size: 14px;
		}
	}
	@include ipad(){
		width: 50%;
	}
	@media (max-width: 667px){
		width: 56%;
	}
	@media (max-width: 568px){
		width: 63%;
		p{
			font-size: 12px;
		}
	}
	@include iphone8(){
		width: 100%;
		margin-top: 0;
		position: unset;
		box-shadow: none;
		div{
			h4:first-letter{
				color: #8a5742;
			}
		}
	}
}
.group:nth-of-type(odd) .txt{
		left: 0%;
}
.group .txt:before{
	content: '';
	display: block;
	width: 100%;
	height: 100%;
	background-color: rgba(86,53,41,.8);
	position: absolute;
	top: 0;
	left: -100%;
	transition: 1.5s;
	z-index: -1;
	@include iphone8(){
		display: none;
	}
}
.group .txt:hover:before{
	left: 0%;
}
.group .txt:hover{
	transition: all .5s;
	color: #fff;
	@include iphone8(){
		color: unset;
	}
}
/*boxA end*/

/*==========================================*/

