#gotop {
    position: fixed;
    right: 20px;
    bottom: 30px;
    font-size: 40px;
    color: rgb(87,38,0);
    cursor: pointer;
    z-index: 1000;
    transition: 1s;
   	display: none;
   	animation: rubberBand 1s linear;

   	@include desktop(){
		right: 40px;
	  bottom: 60px;
	  font-size: 60px;
	}
}

#gotop:hover{
	transition: 1s;
	transform: translateY(-10px);
}